import React from 'react'
import Link from 'gatsby-link'
import styled from 'styled-components'
import breakpoint from "styled-components-breakpoint"

const Nav = styled.nav`
  text-align: center;
  display: flex;
  flex-flow: row;
  justify-content: space-around;
  align-items: center;
  ${props => breakpoint('xs', props.theme.breakpoints)`
    width: 275px;
  `}
  ${props => breakpoint('sm', props.theme.breakpoints)`
    width: 400px;
  `}

`

const NavLink = styled.button`
  color: ${props => props.theme.color.p};
  background-color: white;
  border: 0;
  border-radius: .1rem;
  font-size: 1.25rem;
  padding: 1rem 1rem;
  transition: ${props => props.theme.effects.transition};
  &:hover {
    background-color: ${props => props.theme.color.s};
    box-shadow: ${props => props.theme.effects.shadow[0]};
    cursor: pointer;
  }
  &:focus{
    outline: none;
  }
  ${props => breakpoint('xs', props.theme.breakpoints)`
    padding: .5rem .5rem;
  `}
  &:active{
    background-color: ${props => props.theme.color.s};
    box-shadow: ${props => props.theme.effects.shadow[0]};
    cursor: pointer;
  }
`

const Navigation = ({data}) => (
    <Nav>
        { data.map((item, i)=>(
              <Link
                key={i}
                to={item.link}
                style={{
                  display: 'block',
                  textDecoration: 'none',
                  transition: '250ms all ease'
                }}
              >
              <NavLink>{ item.heading }</NavLink>
              </Link>))
          }
    </Nav>
)

export default Navigation



