import React from 'react'
import PropTypes from 'prop-types'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'

import styled from 'styled-components'
import {ThemeProvider} from 'styled-components'
import theme from '../utils/theme.js'
import breakpoint from "styled-components-breakpoint"

import Logo from "../components/Logo.js"
import Navigation from "../components/Navigation.js"
import {Button} from '../utils/styled-components.js'
import meta from "../utils/metadata.js"
import RegistrationBar from '../components/RegistrationBar.js'

import './index.css'

//styles
const NavWrapper = styled.div`
`
const Ul = styled.ul`
  list-style-type: none;
  overflow: hidden;
`
const Li = styled.li`
  display: inline;
`
const HeaderWrapper = styled.div`
  background: ${props => props.theme.color.l};
  z-index: 99999;
  height: ${props => props.theme.dimensions.headerHeight};
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
`
const HeaderDiv = styled.header`
  margin: 0 auto;
  max-width: 1080px;
  box-sizing: border-box;
  height: 100%;
  padding: .25rem 1.0875rem .25rem 1.0875rem;
  display: flex;
  flex-flow:  column;
  align-items: center;
  justify-content: space-between;
  ${props => breakpoint('lg', props.theme.breakpoints)`
    flex-flow: row;
  `}
`
const Content = styled.main`
  margin: 0 auto;
  z-index: 9;
  margin-top: ${props => props.theme.dimensions.headerHeight};
`
const Spacer = styled.div`
  height: 5rem;
`

const Header = ({data}) => (
  <HeaderWrapper>
    <HeaderDiv>
      <Logo />
      <Navigation data={data.site.siteMetadata.navigation} />
    </HeaderDiv>
  </HeaderWrapper>
)

Header.propTypes = {
  data: PropTypes.object
}

const TemplateWrapper = ({ children, data }) => (
  <div>
    <Helmet
      title="No Smile Left Behind"
      meta={meta}
    />
    <ThemeProvider theme={theme}>
      <div>
        <Header data={data} />
        <Content
        >
          {children()}
        </Content>
        <RegistrationBar/>
        <Spacer/>
      </div>
    </ThemeProvider>

  </div>
)

TemplateWrapper.propTypes = {
  children: PropTypes.func,
  data: PropTypes.object,
}

export const query = graphql`
  query HeaderQuery {
    site {
      siteMetadata {
        title
        logo
        navigation {
          heading
          link
        }
      }
    }
  }
`

export default TemplateWrapper



